import { Injectable } from '@angular/core';
import {BehaviorSubject} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ServiceService {
  private img = new BehaviorSubject<string>('https://newevolutiondesigns.com/images/freebies/flowers-wallpaper-25.jpg');
  private text = new BehaviorSubject<string>('');
  private fontSize = new BehaviorSubject<string>('28');
  private shadow = new BehaviorSubject<boolean>(false);
  private color = new BehaviorSubject<string>('#ffffff');
  private save = new BehaviorSubject<boolean>(false);
  constructor() { }
  setImg(url) {
    this.img.next(url);
  }
  getImg() {
    return this.img.asObservable();
  }
  setText(url) {
    this.text.next(url);
  }
  getText() {
    return this.text.asObservable();
  }
  setFontSize(size) {
    this.fontSize.next(size);
  }
  getFontSize() {
    return this.fontSize.asObservable();
  }
  setShadow(bool) {
    this.shadow.next(bool);
  }
  getShadow() {
    return this.shadow.asObservable();
  }
  setColor(color) {
    this.color.next(color);
  }
  getColor() {
    return this.color.asObservable();
  }

  setSave(value) {
    this.save.next(value);
  }
  getSave() {
    return this.save.asObservable();
  }

}
