import { Component, OnInit } from '@angular/core';
import {ServiceService} from '../service.service';

@Component({
  selector: 'app-edit-img',
  templateUrl: './edit-img.component.html',
  styleUrls: ['./edit-img.component.css']
})
export class EditImgComponent implements OnInit {
urlImage = '';
text = '';
color = '';

  constructor(private service: ServiceService) { }

  ngOnInit() {
    this.service.getImg().subscribe(message => {
      this.urlImage = message;
    });
    this.service.getColor().subscribe(message => {
      this.color = message;
    });
  }
  inputImage(event: any) { // without type info
    this.urlImage = event.target.value;
    this.service.setImg( this.urlImage );
  }
  inputText(event: any) { // without type info
    this.text = event.target.value;
    this.service.setText(this.text);

  }
  textSize(value) {
    this.service.setFontSize(value.target.value);
  }

  shadow(value) {
    this.service.setShadow(value.target.checked);
   // this.service.setShadow(value.target.value);
  }
  selectColor(value) {
    this.service.setColor(value.target.value);
  }
  save(value) {
    this.service.setSave(value);
  }

}
