import {Component, OnInit, ViewChild} from '@angular/core';
import {ServiceService} from '../service.service';
import domtoimage from 'dom-to-image';

@Component({
  selector: 'app-view-img',
  templateUrl: './view-img.component.html',
  styleUrls: ['./view-img.component.css']
})
export class ViewImgComponent implements OnInit {
  img: string;
  text: string;
  fontSize: any;
  isShadow: boolean;
  color: string;
  @ViewChild('parent') parent;



  constructor(private service: ServiceService) {
    this.img = '';

  }

  ngOnInit() {
    this.service.getText().subscribe(message => {
    this.text = message;
});

    this.service.getImg().subscribe(message => {
      this.img = message;
    });

    this.service.getFontSize().subscribe(message => {
      this.fontSize = message;
    });

    this.service.getShadow().subscribe(message => {
      this.isShadow = message;
    });
    this.service.getColor().subscribe(message => {
      this.color = message;
    });

    this.service.getSave().subscribe(message => {
      if (message) {
        domtoimage.toJpeg(this.parent.nativeElement, { quality: 1 })
          .then(function (dataUrl) {
            const link = document.createElement('a');
            link.download = 'img.jpeg';
            link.href = dataUrl;
            link.click();
          });

      }
    });
  }
}
